.songs-pink {
    background: rgba(0,0,0,.3);
    height: 95vmin;
    width: 100vmin;
    position: relative;
    cursor: pointer;
}

.songs-pink:before {
    content:'';
    background: url('../assets/221016-BLACKPINK-World-Tour-BORN-PINK-at-Seoul-documents-1.jpeg') no-repeat center center/cover;
    height: 95vmin;
    width: 100vmin;
    position: absolute;
    top: 0;
    left: 0;
    z-index: -1;
}

.songs-pink .heading {
    width: 100vmin;
    height: 100vmin;
    display: flex;
    flex-direction: column;
    justify-content: center;
    align-items: center;
}

.songs-pink h1 {
    font-size: 3rem;
}

.songs-pink p {
    font-size: 1.4rem;
}

@media screen and (max-width:640px) {
    .songs-pink h1 {
        font-size: 2rem;
    }
};