
#video {
    position: fixed;
    width: 100%;
    height: 100%;
    object-fit: cover;
    z-index: -1;
    cursor: pointer;
}

.hero {
    height: 100vh;
    width: 100%;
    object-fit: contain;
    cursor: pointer;
}

.hero .content {
    text-align: center;
    position: absolute;
    margin: auto;
    width: 100%;
    height: 100%;
    top: 40vh;
}

.hero .content h1 {
    font-size: 4rem;
    color: rgb(255, 255, 255);
    padding: 3rem;
}

.hero p {
    font-size: 2.5rem;
    font-weight: 200;
    text-transform: uppercase;
    margin-bottom: 1.5rem;
    color: rgb(255, 255, 255);
}

.hero .btn {
    font-size: 1.5rem;
    margin: 1rem 0rem;
    padding: 0.5rem;
    
    font-size: 1rem;
}

@media screen and (max-width:640px) {
    .content h1 {
        font-size: 3rem;
        cursor: pointer;
    }

    .content p {
        font-size: 1.4rem;
        margin-bottom: 1.6rem;
        cursor: pointer;
    }
}



