/* JISOO KIM MEMBER SECTION */
.members {
    background-color: #000000;
    width: 81%;
    margin: auto;
    display: grid;
    grid-gap: 2px;
    padding: 0rem;
    grid-template-columns: repeat(1, 4fr);
}


.members-container .card {
    border: 2px solid #eee;
    color: #eee;
   
}

.card:hover {
    background-color: #141414;
    background: url('../assets/blackpink_kill_this_love_japanese_concept_jisoo2.jpeg') no-repeat center center/cover;
}

.card {
    text-align: center;
    padding: 1rem;
}

.card h3 {
    font-size: 2rem;
    padding: 1rem;
}

.card .bar {
    border-bottom: 1px solid #eee;
    width: 50%;
    margin: 0rem auto;
    display: block;
}

.card .YG {
    font-size: 4rem;
    font-weight: 600;
    padding: 1rem;
}

p {
    color: #eee;
    padding: 0rem 0rem;
    font-size: 1rem;
}

.card .btn {
    display: block;
    width: 100%;
    margin: 7rem 0rem auto;
    font-size: 1rem;
}

@media screen and (max-width: 980px) {
    .members-container {
        max-width: 100%;
        margin: auto;
        grid-template-columns: 1fr;
    }
}
/* END OF JISOO KIM MEMBER SECTION */

